* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  color: #f0f0f0;
  background-color: #262424;
  font-family: Helvetica Neue, Arial, sans-serif;
}

.date-time {
  color: #f0f0f0;
  margin-left: 130px;
  font-family: Georgia, serif;
  font-size: 20px;
}

.navbar {
  color: #fff;
  z-index: 1000;
  background-color: #1b1b1b;
  justify-content: space-between;
  align-items: center;
  padding: 15px 20px;
  display: flex;
  position: sticky;
  top: 0;
  box-shadow: 0 4px 10px #0000004d;
}

.logo {
  letter-spacing: 1.5px;
  font-family: Georgia, serif;
  font-size: 28px;
  font-weight: bold;
}

.nav-links {
  gap: 25px;
  font-family: Georgia, serif;
  font-weight: bold;
  list-style: none;
  display: flex;
}

.nav-links li {
  position: relative;
}

.nav-links a {
  color: #f0f0f0;
  border-radius: 5px;
  padding: 8px 15px;
  text-decoration: none;
  transition: color .3s, background-color .3s;
}

.nav-links a:hover {
  color: #fff;
  background-color: #444;
}

.hamburger-menu {
  cursor: pointer;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  width: 30px;
  height: 20px;
  display: none;
}

.hamburger-menu span {
  background-color: #fff;
  width: 100%;
  height: 3px;
  display: block;
}

.hero-section {
  text-align: center;
  background: url("https://res.cloudinary.com/dphvhssec/image/upload/v1727099595/kiks/j9usu0njfwzy6h6ywi5p.jpg") center / cover no-repeat;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 500px;
  display: flex;
  position: relative;
  box-shadow: inset 0 0 0 1000px #00000080;
}

.hero-section h1 {
  color: #fff;
  text-transform: uppercase;
  letter-spacing: 3px;
  text-shadow: 2px 2px 8px #000000b3;
  font-family: Georgia, serif;
  font-size: 56px;
  font-weight: bold;
}

.main-content {
  flex-wrap: wrap;
  justify-content: space-between;
  padding: 40px 20px;
  display: flex;
}

.left-column {
  width: 100%;
}

.top-stories {
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 25px;
  display: grid;
}

.story {
  text-align: center;
  background-color: #2c2c2c;
  border: 1px solid #444;
  border-radius: 10px;
  padding: 20px;
  transition: transform .3s, box-shadow .3s;
  position: relative;
  box-shadow: 0 4px 12px #0000001a;
}

.story:hover {
  background-color: #3a3a3a;
  transform: translateY(-5px);
  box-shadow: 0 8px 20px #0003;
}

.story img {
  border-radius: 10px;
  width: 100%;
  margin-bottom: 15px;
}

.story h2 {
  color: #f0f0f0;
  text-align: center;
  margin-top: 10px;
  font-size: 24px;
  line-height: 1.4;
}

.story p {
  color: #ccc;
  margin: 10px 0;
  line-height: 1.6;
}

.story a {
  color: #0095ff;
  margin-top: 10px;
  font-weight: bold;
  text-decoration: none;
  transition: color .3s;
  display: inline-block;
}

.story a:hover {
  color: #007acc;
  text-decoration: underline;
}

.story:after {
  content: "";
  background-color: #0095ff;
  width: 50%;
  height: 3px;
  margin: 15px auto 0;
  display: block;
}

footer {
  color: #fff;
  text-align: center;
  background-color: #1b1b1b;
  padding: 30px 20px;
}

.footer-content {
  max-width: 1200px;
  margin: auto;
}

.footer-content a {
  color: #fff;
  margin: 0 10px;
  text-decoration: none;
  transition: color .3s;
}

.footer-content a:hover {
  color: #ddd;
  text-decoration: underline;
}

.footer-content p {
  margin: 10px 0;
}

.footer-image {
  max-width: 150px;
  margin-top: 20px;
}

@media (width <= 768px) {
  .navbar {
    flex-direction: column;
    align-items: flex-start;
    padding: 10px;
  }

  .date-time {
    text-align: left;
    margin-bottom: 10px;
    margin-left: 0;
    font-size: 18px;
  }

  .logo {
    margin-bottom: 10px;
  }

  .nav-links {
    z-index: 1000;
    background-color: #1b1b1bf2;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
    padding: 20px 0;
    transition: opacity .2s;
    display: none;
    position: fixed;
    top: 0;
    left: 0;
  }

  .nav-links.active {
    opacity: 1;
    display: flex;
  }

  .nav-links:not(.active) {
    opacity: 0;
  }

  .hamburger-menu {
    z-index: 1001;
    margin-left: auto;
    display: flex;
  }

  .main-content {
    flex-direction: column;
    padding: 20px;
  }

  .top-stories {
    grid-template-columns: 1fr;
  }

  .nav-links a {
    text-align: center;
    color: #f0f0f0;
    width: 100%;
    padding: 10px 15px;
    font-size: 24px;
    text-decoration: none;
    transition: color .3s;
  }

  .nav-links a:hover {
    color: #0095ff;
  }
}
